import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { apiUrl } from '../../../AxiosServices';
import FileUpload from '../upload/FileUpload';

const styles = (theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(2),
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
});

const DialogTitle = withStyles(styles)((props) => {
    const { children, classes, onClose, ...other } = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root} {...other}>
            <Typography color={"primary"} variant="h6">{children}</Typography>
            {onClose ? (
                <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
                    <CloseIcon />
                </IconButton>
            ) : null}
        </MuiDialogTitle>
    );
});

const DialogContent = withStyles((theme) => ({
    root: {
        padding: theme.spacing(2),
    },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
    root: {
        margin: 0,
        padding: theme.spacing(1),
    },
}))(MuiDialogActions);

export default function ModalVideo({ role, open, type, module, handleClose }) {
    let link = apiUrl;
    if (module) {
        link += (type === 'Capsule') ? module.video : module.enregistrement;
    }
    const getVideo = () => {
        if (type === 'Enregistrement' && role === 2) {
            return (
                !module.enregistrement ?
                    <FileUpload id={module.id} /> : <video style={{ width: '100%' }} className="video" controls={true}>
                        <source src={link}
                            type="video/mp4"></source>
                    </video>
            )
        }
        if (type === 'Enregistrement' && role !== 2) {
            return (
                !module.enregistrement ?
                    <h1 style={{ textAlign: "center", color: "#b3294e" }}>la vidéo n'est pas encore disponible </h1>
                    : <video style={{ width: '100%' }} className="video" controls={true}>
                        <source src={link}
                            type="video/mp4"></source>
                    </video>
            )
        }
        if(type=== 'Capsule') {
            return (

                module.video ?
                    <video style={{ width: '100%' }} className="video" controls={true}>
                        <source src={link}
                            type="video/mp4"></source>
                    </video> :

                    <h1 style={{ textAlign: "center", color: "#b3294e" }}>la vidéo n'est pas encore disponible </h1>


            )

        }
    }
    return (
        <Dialog maxWidth={'md'} onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
            <DialogTitle id="customized-dialog-title" onClose={handleClose}>
                {type} {' '}Video  : {module && module.titre}
            </DialogTitle>
            <DialogContent dividers style={{ textAlign: 'center' }}>
                {
                    getVideo()
                }



            </DialogContent>
            <DialogActions>
                <Button autoFocus onClick={handleClose} color="primary">
                    Terminer
                    </Button>
            </DialogActions>
        </Dialog>

    );
}
