import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { Typography, Grid, Button, Container } from '@material-ui/core';
import { useParams } from 'react-router';
import CardItem from './CardItem';
import {getModuleById} from '../../../AxiosServices/CoursService';
import Loading from '../Loading/Loading';
import ModalPdf from './ModalPdf';
import ModalVideo from './ModalVideo';
import { AuthContext } from '../../Providers/AuthProvider';

export default function ModuleToLean(props) {
    const classes = useStyles();
    const theme = useTheme();
    const [Module, setModule] = React.useState({});
    const [openPdf,setOpenPdf]=React.useState(false);
    const [openVideo,setOpenVideo]=React.useState(false);
    const auth = React.useContext(AuthContext);
    const [type,setType]=React.useState(null);
    const [quizPassed, setQuizPassed] = React.useState(false);
    const [loading,setLoading]=React.useState(true);
    const { id } = useParams();
    const image1 = `${process.env.PUBLIC_URL}/images/logged_out/image1.jpg`
    const image2 = `${process.env.PUBLIC_URL}/images/logged_out/image2.jpg`
    const image3 = `${process.env.PUBLIC_URL}/images/logged_out/image3.jpg`
    const {
        selectPosts,
    } = props;
    const handleClickOpenPdf = () => {
        setOpenPdf(true);
      };
      const handleClosePdf = () => {
        setOpenPdf(false);
      };
      const handleClickOpenVideo = (type) => {
        setOpenVideo(true);
      };
      const handleCloseVideo = () => {
        setOpenVideo(false);
      };
      const OpenVideo =(type)=>{
          setType(type);
          handleClickOpenVideo();
      }
    React.useEffect(()=>{
        getModuleById(id).then((r)=> {
            selectPosts();
            setModule(r.data.data)
            setLoading(false);
        }).catch((e)=> {
            setLoading(false);
            alert('verifier votre connexion');
        })
    },[])

    if(loading)
    {
        return <Loading></Loading>
    }
    return (
        <div className={classes.root}>
            <h1 className={classes.text} >{Module.slug}  وحدة التكوين</h1>
            <p className={classes.subtext} style={{fontWeight:'bold'}}> {Module.titre_ar} </p>
            <p className={classes.subtext}> {Module.titre} </p>
            <Grid container spacing={1}>

                <Grid
                    item
                    xs={12}
                    sm={12}
                    md={4}
                >
                    <CardItem isOwner={true} open={handleClickOpenPdf} image={image1} text={" Voir le support "} />
                </Grid>
                <Grid
                    item
                    xs={12}
                    sm={12}
                    md={4}
                >
                        <CardItem  isOwner={true} open={()=>{OpenVideo('Capsule')}} image={image2} text={" Voir la capsule vidéo "} />
                </Grid>
                <Grid
                    item
                    xs={12}
                    sm={12}
                    md={4}
                >
                    <CardItem isOwner={true} open={()=>{OpenVideo('Enregistrement')}}  image={image3} text={" Voir l'enregistrement de la vidéo "} />
                </Grid>
            </Grid>
            <ModalPdf  handleClose={handleClosePdf} open={openPdf}  module={Module}  ></ModalPdf>
            <ModalVideo  handleClose={handleCloseVideo} role={auth.user.role_id} open={openVideo} type={type} module={Module}></ModalVideo>
        </div>
    );
}
const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: theme.palette.background.paper,
        width: '100%',
        marginLeft: "4%",
        marginTop:'4%',
        padding:'2%'
    },
    borderDiv: {
        border: "1px solid #e0e4e6"
    },
    media: {
        height: '60%',
    },
    size: {
        fontSize: '20px'
    },
    text : {
        textAlign:"center",
        color:'#b3294e'
    },
    subtext : {
        textAlign:"center",
        color:'#4829B2',
        fontSize:'20px'
    }
   
}));
ModuleToLean.propTypes = {
    selectPosts: PropTypes.func.isRequired,
};

