import React, { useEffect } from "react";
import { Grid, Box, Button, withWidth, makeStyles } from "@material-ui/core";
import MailIcon from '@material-ui/icons/Mail';
import LinkedInIcon from '@material-ui/icons/LinkedIn';

function Sherapeur({ sh }) {
    return (

        <Grid container style={{ backgroundColor: "white", padding: "15px", boxShadow: "4px 10px 16px -2px rgba(0,0,0,0.56)" }} >
            <Grid

                item
                sm={3}
                lg={3}
            >
                <img style={{ height: "150px", width: "100px" }} src={sh.image}></img>


            </Grid>

            <Grid

                item
                sm={9}
                lg={9}
            >
                <div style={{ display: "flex", flexDirection: 'column' }}>
                    <h3 style={{ marginTop: "0px", marginBottom: "0px" }}>{sh.nom} {sh.prenom} <br/> <span style={{opacity:'0.5'}} >{sh.specialite}</span></h3>
                    <p style={{ marginTop: "10px", marginBottom: "0px",color: "#4829B2"  }}>{sh.description}</p>
                    <div style={{ marginTop: "10px", display: "flex",alignItems:"center" }} >
                        <MailIcon style={{color:'#b3294e'}}></MailIcon>
                        <span>{sh.email}</span>
                    </div>
                    <div style={{marginTop: "10px", display: "flex",alignItems:"center" }}>
                        <LinkedInIcon style={{color:'#0073b1'}} ></LinkedInIcon>
                        <span>In/OwerLinkedin</span>
                    </div>
                </div>
            </Grid>
{/*            <Grid

                item
                sm={3}
                lg={3}
            >
                <div style={{ display: "flex", flexDirection: 'column' }}>
                    <Box component="fieldset" mb={3} borderColor="transparent" style={{ marginBottom: 0 }}>
                        <Rating name="read-only" value={sh.value} readOnly />
                    </Box>
                    <br/>
                    <Button size="small" variant="contained" color="primary" onClick={() => {

                    }}> Contacter  </Button>
                    <br/>
                    <Button size="small"  variant="contained" color="secondary" onClick={() => {

                    }}> Voir le profil  </Button>
                </div>
            </Grid>*/}
        </Grid>

    );
}

export default Sherapeur;