import React from 'react';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Viewer, { Worker } from '@phuocng/react-pdf-viewer';
import '@phuocng/react-pdf-viewer/cjs/react-pdf-viewer.css';
import { apiUrl } from '../../../AxiosServices';


function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    <Typography component={'div'}>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `full-width-tab-${index}`,
        'aria-controls': `full-width-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        backgroundColor: theme.palette.background.paper,
        width: '100%',
    },
    borderDiv: {
        border: "1px solid #e0e4e6"
    }
}));

export default function CustumTabs({ module }) {
    const classes = useStyles();
    const theme = useTheme();
    const [value, setValue] = React.useState(0);
    let linkPdf = 'https://cors-anywhere.herokuapp.com/' + apiUrl;
    let link_video = apiUrl
    if (module) {
        linkPdf += module.file
        link_video += module.video
    }

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const handleChangeIndex = (index) => {
        setValue(index);
    };

    return (
        <div className={classes.root}>
            <AppBar position="static" color="default">
                <Tabs
                    value={value}
                    onChange={handleChange}
                    indicatorColor="primary"
                    textColor="primary"
                    variant="fullWidth"
                    aria-label="full width tabs example"
                >
                    <Tab label="Support" {...a11yProps(0)} />
                    <Tab label="Capsule Video" {...a11yProps(1)} />
                    <Tab label="Bibliographie" {...a11yProps(2)} />
                </Tabs>
            </AppBar>
            <SwipeableViews
                axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                index={value}
                onChangeIndex={handleChangeIndex}
            >
                <TabPanel className={classes.borderDiv} value={value} index={0} dir={theme.direction}>
                    <div>
                        <Worker workerUrl="https://unpkg.com/pdfjs-dist@2.5.207/build/pdf.worker.min.js">
                            <div style={{ margin: "1rem", height: "40rem" }} >
                                <Viewer fileUrl={linkPdf} />
                            </div>
                        </Worker>

                    </div>
                </TabPanel>
                <TabPanel className={classes.borderDiv} value={value} index={1} dir={theme.direction}>
                    <div style={{ display: 'flex', justifyContent: "center", alignItems: "center" }}>
                        {
                            module.video ?
                                <video style={{ width: '80%' }} className="video" controls={true}>
                                    <source src={link_video}
                                        type="video/mp4"></source>
                                </video> :
                                <h1 style={{ textAlign: "center", color: "#b3294e" }}>la vidéo n'est pas encore disponible </h1>
                        }

                    </div>
                </TabPanel>
                <TabPanel className={classes.borderDiv} value={value} index={2} dir={theme.direction}>
                    <div>
                        <ul>
                            {
                                module.bilbilo && module.bilbilo.map((e) => {
                                    return (<li>{e}</li>)
                                })
                            }

                        </ul>
                    </div>
                </TabPanel>
            </SwipeableViews>
        </div>
    );
}