import React, { Fragment, useState } from 'react';
import { Button } from '@material-ui/core';
import Alert from '@material-ui/lab/Alert';
import Progress from './Progress';
import { apiUrl } from '../../../AxiosServices';
import axios from 'axios';
import axiosinstance from '../../../AxiosServices/instance';
import './upload.css';

const FileUpload = ({ id }) => {
  const [file, setFile] = useState('');
  const [filename, setFilename] = useState('Choissiez la video');
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [uploadPercentage, setUploadPercentage] = useState(0);

  const onChange = e => {
    setFile(e.target.files[0]);
    setFilename(e.target.files[0].name);
  };

  const onSubmit = async e => {
    e.preventDefault();
    const formData = new FormData();
    formData.append('enregistrement', file);


    setMessage('');
    setLoading(true);
    axiosinstance.post(apiUrl + '/api/formateur/module/' + id + '/uploadenrg', formData).then((r) => {
      setMessage('Video est chargé');
      setLoading(false);
      setTimeout(() => window.location.reload(), 3000);
    }).catch((e) => {
      setMessage("la vidéo n'a pas pu se charger ");
      setLoading(false);
    });


  };

  return (
    <Fragment>
      <form onSubmit={onSubmit}>
        <div className='custom-file mb-4'>
          <input
            type='file'
            className='custom-file-input'
            id='customFile'
            onChange={onChange}
            required
          />
          <label className='custom-file-label' htmlFor='customFile'>
            {filename}
          </label>
        </div>
        <div style={{ marginTop: "2%" }}>
          {
            loading && <Progress />
          }


          <Button type='submit' variant="contained" color="primary">
            Uploade
       </Button>

        </div>


      </form>
      { message != '' ? <Alert severity="info">{message}</Alert> : null}
    </Fragment>
  );
};

export default FileUpload;
