import React from 'react';
import { makeStyles, Grid } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Rating from '@material-ui/lab/Rating';
import Box from '@material-ui/core/Box';
import { useHistory } from "react-router-dom";


export default function QuizItem({ role, module, go }) {
  const classes = useStyles();
  const history = useHistory();
  return (
    <Card className={classes.root}>
      <CardActionArea style={{ height: '30vh' }}>
        {/*        <CardMedia
          className={classes.media}
          image={image}
          title={titre}
      />*/}
        <CardContent>
          <Typography className={classes.size} gutterBottom variant="h5" component="h2" style={{ textAlign: "center", color: "#b3294e", fontWeight: 'bold' }}>
            {module.slug}
          </Typography>

          <Typography variant="body2" style={{ marginTop: "3%", textAlign: 'center', fontSize: '18px', fontWeight: 'bold' }} color="textSecondary" component="p">
            {module.titre_ar}
          </Typography>
          <Typography variant="body2" style={{ marginTop: "3%", textAlign: 'center', fontWeight: 'bold' }} color="textSecondary" component="p">
            {module.titre}
          </Typography>


        </CardContent>
      </CardActionArea>

      <CardActions  >
        {
          role == 2 ?
           <div style={{textAlign:'center'}}>
             <Button size="small" variant="contained" color={module.is_live ? "primary" : "secondary" }  onClick={() => go()}> {module.is_live ?  'Arreter la session':'Démarer la session'} </Button> 
           </div>
           :
            <div>
              <Button size="small" variant="contained" disabled={!module.is_live || !module.is_owner} color="primary" onClick={() => go()}> Joindre la session </Button>
              <span style={{ color: module.is_live ? 'green' : 'red' }}> {module.is_live ? 'Live' : 'Not Live'} </span>
            </div>

        }

      </CardActions>

    </Card>
  );
}
const useStyles = makeStyles({
  root: {
    maxWidth: 345,
    height: '38vh'
  },
  media: {
    height: 140,

  },
  size: {
    fontSize: '20px'
  }
});