import React, { Fragment, useState, useEffect } from "react";
import PropTypes from "prop-types";
import { Grid, Button } from "@material-ui/core";
import QuizItem from '../Quiz/QuizItem';
import Loading from '../Loading/Loading';
import ModalQuiz from '../Quiz/ModalQuiz';
import { getModules } from '../../../AxiosServices/CoursService';
import { useHistory } from "react-router-dom";
import { AuthContext } from '../../Providers/AuthProvider'
import CachedIcon from '@material-ui/icons/Cached';


function Dashboard(props) {
  const {
    selectDashboard,
  } = props;
  const history = useHistory();
  const [loading, setLoading] = React.useState(true);
  const { user } = React.useContext(AuthContext);
  const [Modules, setModules] = React.useState([]);
  const [open, setOpen] = useState(false);
  const [selectedModule, setSelectedModule] = useState(null);
  useEffect(() => {
    selectDashboard()
    getModules().then((r) => {
      setModules(r.data.data);
      setLoading(false);
    }).catch((e) =>{
      setLoading(false);
      alert('verifier votre connexion');
    })

  }, [])
  const refresh = ()=>{
    getModules().then((r) => {
      setModules(r.data.data);
      setLoading(false);
    }).catch((e) => {
      setLoading(false);
      alert('verifier votre connexion');
    })
  }
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const go = (e) => {
    setSelectedModule(e) 
    if( user.role_id == 4 ){
      console.log("mn lakher "+user.role_id)
      window.open(e.live_url + '&username=' + user.nom_complet + "&r=" + user.role_id +"&formateur="+e.formateur_live.nom_complet, '_blank');
    }else{
    if (e.is_evaluted)
    {
     window.open(e.live_url + '&username=' + user.nom_complet + "&r=" + user.role_id +"&formateur="+e.formateur_live.nom_complet, '_blank');
     history.push('/c/Evaluate/'+e.id);
    }
    else{
      handleClickOpen();
    }
  }
    // if (e.is_evaluted)
    // {
    //  window.open(e.live_url + '&username=' + user.nom_complet + "&r=" + user.role_id +"&formateur="+e.formateur_live.nom_complet, '_blank');
    //  history.push('/c/Evaluate/'+e.id);
    // }
    // else
    //   handleClickOpen();
  }
  if (loading)
    return (<Loading></Loading>)

  return (
    <Fragment>
      
       <Button startIcon={<CachedIcon />} size="small"  variant="contained" color="primary" onClick={()=> {refresh()}} style={{float:"right"}} >Actualiser</Button>
      <ModalQuiz open={open} module={selectedModule} handleClose={handleClose} goToQuiz={() => {
        history.push('/c/Quiz/' + selectedModule.id);
      }} />
      {

        <Grid style={{ marginLeft: '2%' }} container spacing={3}>
          {
            Modules.map((e, index) => {
              return (
                <Grid
                  key={index}
                  item
                  xs={12}
                  sm={6}
                  lg={4}
                  data-aos="zoom-in-up" 
                >
                  <QuizItem
                    module={e}
                    go={() => { go(e) }}
                  />
                </Grid>

              )

            })

          }
        </Grid>

      }
    </Fragment>
  );
}

Dashboard.propTypes = {
  CardChart: PropTypes.elementType,
  statistics: PropTypes.object.isRequired,
  toggleAccountActivation: PropTypes.func,
  pushMessageToSnackbar: PropTypes.func,
  targets: PropTypes.arrayOf(PropTypes.object).isRequired,
  setTargets: PropTypes.func.isRequired,
  isAccountActivated: PropTypes.bool.isRequired,
  selectDashboard: PropTypes.func.isRequired,
};

export default Dashboard;
