import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import {getEvaluationState} from '../../../AxiosServices';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
  title :{
    color:"#b3294e", fontSize:18,fontWeight:"bold"
  }
});

function createData(name, calories, fat, carbs, protein) {
  return { name, calories, fat, carbs, protein };
}


export default function EvaluationStat({id}) {
    React.useEffect(()=> {
        getEvaluationState(id).then((r)=>{
            setData(r.data.data);
        })
    },[])
  const classes = useStyles();
  const [data,setData]=React.useState([]);

  return (
    <TableContainer style={{marginLeft: '1%',marginTop:"2%",width:"70%"  }} component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell className={classes.title} align="right">ضعيف</TableCell>
            <TableCell className={classes.title} align="right">متوسط</TableCell>
            <TableCell className={classes.title} align="right">حسن</TableCell>
            <TableCell className={classes.title} align="right">ممتاز</TableCell>
            <TableCell className={classes.title}  align="right">السؤال</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data.evaluationquestions ? data.evaluationquestions.map((q) => (
            <TableRow key={q.id}>
              <TableCell align="right">{q.reponses[0].nb_votes}</TableCell>
              <TableCell align="right">{q.reponses[1].nb_votes}</TableCell>
              <TableCell align="right">{q.reponses[2].nb_votes}</TableCell>
              <TableCell align="right">{q.reponses[3].nb_votes}</TableCell>
              <TableCell align="right" component="th" scope="row">
                {q.body}
              </TableCell>
            </TableRow>
          )):
          <div >
              <h1 className={classes.title} style={{textAlign:"center"}}>Pas de données </h1>
          </div>
          }
        </TableBody>
      </Table>
    </TableContainer>
  );
}