import React from 'react';
import { Button, Box } from '@material-ui/core';
import { useHistory } from 'react-router-dom'

function Thanks({text}) {
    const history = useHistory();
    return (
        <Box style={{ display: "flex",height:"45vh",marginLeft:"4%", alignItems: "center", justifyContent: "center", backgroundColor: "white" }}>
            <div>
                <h1 style={{ textAlign: "center" ,color:"#b3294e"}}>
                    {text}
                </h1>
                <div style={{ display: "flex", justifyContent: 'center' }}>
                    <Button
                        variant="contained"
                        color="primary"
                        style={{ marginBottom: '2%', marginRight: "2%" }}
                        onClick={() => {  history.push('/c/Formations') }}
                    >
                        Revenir aux formations
                            </Button>

                </div>

            </div>
        </Box>
    );
}

export default Thanks;