import React from 'react';
import { useParams } from 'react-router-dom';
import PreQuiz from './PreQuiz';
import PropTypes from "prop-types";
import { Grid, Divider, makeStyles } from "@material-ui/core";
import EvaluationStat from './EvaluationStat'
function ModuleStat(props) {
    const { id } = useParams()
    React.useEffect(() => {
        selectSubscription()
    })
    const {
        selectSubscription,
    } = props;
    return (
        <div>
            <Grid style={{ marginLeft: '2%' }} container spacing={3}>

                <Grid
                    sm={12}

                >
                    <PreQuiz id={id} />

                </Grid>
            </Grid>
            <Grid style={{ marginLeft: '2%' }} container spacing={3}>

                <Grid
                    sm={12}
                    style={{display:"flex",alignItems:"center",justifyContent:"center", flexDirection:"column"}}
                >
                    <Divider style={{marginTop:"3%" , color:"primary"}}></Divider>
                    <h1 style={{textAlign:"center",color:"#b3294e"}}>les retours des participants sur la formations</h1>
                    <EvaluationStat id={id} />

                </Grid>
            </Grid>

        </div>
    );
}
ModuleStat.propTypes = {
    selectSubscription: PropTypes.func.isRequired,
};
export default ModuleStat;