import React from 'react';
import { makeStyles, Grid } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Rating from '@material-ui/lab/Rating';
import Box from '@material-ui/core/Box';
import CircularProgress from '@material-ui/core/CircularProgress';
import { useHistory } from "react-router-dom";

const progressCircule = (pourcentage) => {
    return (
        <Box position="relative" display="inline-flex">
            <CircularProgress variant="static" value={pourcentage} />
            <Box
                top={0}
                left={0}
                bottom={0}
                right={0}
                position="absolute"
                display="flex"
                alignItems="center"
                justifyContent="center"
            >
                <Typography variant="caption" component="div" color="textSecondary">{`${Math.round(
                    pourcentage,
                )}%`}</Typography>
            </Box>
        </Box>
    )
}


export default function QuizItem({ id, titre, image, description, go,role,isOwner }) {
    const classes = useStyles();
    const history = useHistory();

    return (
        <Card className={classes.root}>
            <CardActionArea style={{ height: '43vh' }}>
                <CardMedia
                    className={classes.media}
                    image={image}
                    title={titre}
                />
                <CardContent>
                    <Typography className={classes.size} gutterBottom variant="h5" component="h2" style={{ textAlign: "center" }}>
                        {titre}
                    </Typography>

                    <Typography variant="body2" color="textSecondary" component="p">
                        {description}
                    </Typography>


                </CardContent>
            </CardActionArea>

            <CardActions style={{ justifyContent: 'space-between' }}>
                <Button size="small" disabled={!isOwner} variant="contained" color="primary" 
                onClick={() =>  go() }> {role ===3 ? 'Commencer la formation' : 'Voir la formation'} </Button>
            </CardActions>

        </Card>
    );
}
const useStyles = makeStyles({
    root: {
        maxWidth: 345,
        height: '52vh'
    },
    media: {
        height: '60%',

    },
    size: {
        fontSize: '20px'
    }
});