import * as React from 'react';
import { Grid } from '@material-ui/core';
export default function Answer({selected,image,select,text}) {
    return (
        <Grid item xs={12} md={6}>
            <div style={{ 
                        backgroundColor:  selected ? '#b3294e' : "white", 
                        borderRadius:10,
                       // border: selected ? "solid 2px #4829B2":"none",
                        padding:"3%",
                        '&:hover': {
                            backgroundColor: "#b3294e", 
                         },
                        }} 
                        className="Card" 
                        onClick={()=>{select()}}>
                <p style={{ margin: 0, fontWeight: "bold", color: selected? 'white':'black', textAlign: "center" }} >
                    {text}
                </p>
            </div>
        </Grid>
    );
};