import React from 'react';
import Button from '@material-ui/core/Button';
import {Dialog,Divider} from '@material-ui/core';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { AuthContext } from '../../Providers/AuthProvider'

export default function ModalResultat({ open, handleClose, reponses, totalQst, module_id, history ,is_live,lien}) {
    const { user } = React.useContext(AuthContext);
    const answers = [
        {
            text: "correctes",
            number: reponses,
            color: "#8bc64d"
        },
        {
            text: "fausses",
            number: totalQst - reponses,
            color: "#db3e36"
        },
    ]
    return (
        <div>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle color='primary' id="alert-dialog-title">{"Resultat de Quiz "}</DialogTitle>
                <Divider></Divider>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        <div style={{textAlign:"center"}}>
                           <p style={{fontSize:'43px'}}> Votre résultat est : <span style={{fontWeight:'bold'}}> {Math.floor( (reponses / totalQst) * 100 ) } % </span></p> 

                        </div>
                        <div style={{
                            height: 100,
                            borderRadius: 10,
                            justifyContent: "space-around",
                            flexDirection: "row",
                            display: 'flex',
                            alignItems: "center",
                            margin: "auto",
                            width: "50%"
                        }}>
                            {
                                answers.map((el, index) => (
                                    <div style={{ alignItems: "center" }} key={index}>
                                        <div style={{
                                            height: 50,
                                            width: 50,
                                            backgroundColor: el.color,
                                            borderRadius: 25,
                                            display: "flex",
                                            alignItems: "center",
                                            justifyContent: "center"
                                        }}>
                                            <p style={{ fontWeight: "bold", color: "#fff", fontSize: 20 }}>{el.number}</p>
                                        </div>
                                        <p style={{ margin: 0, fontWeight: "600" }}>Réponses</p>
                                        <p style={{ margin: 0, fontWeight: "600" }}>{el.text}</p>
                                    </div>
                                ))
                            }
                        </div>
                    </DialogContentText>
                </DialogContent>
                <Divider></Divider>
                <DialogActions style={{justifyContent:'space-around'}}>
                    <Button onClick={() => { history.push('/c/Module/' + module_id) }} color="primary"  autoFocus>
                        Aller à la formation
                     </Button>
                     <Button onClick={() => { 
                          history.push('/c/Interactive') }} disabled={!is_live} color="secondary" autoFocus>
                        Aller à la formation distancielle ({is_live ? 'Live': 'Not live'})
                     </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}
