import React from 'react';
import Answer from './Answer';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

function Qst({ number, question, setAnswer ,openQst}) {
    const classes = useStyles();
    const [answer, getAnswer] = React.useState(null);
    const handleChange = (event) => {
        setAnswer({
            question: question.id,
            reponse:  event.target.value 
        }
        );
    };
    return (
        <div style={{ padding: '2%' }}>
            <div>
                <p className={classes.pQst}>Question {number + 1}:</p>
                <h3 style={{ margin: 10, textAlign: "center" }}>{question.body}</h3>
            </div>
            <div className={classes.divAnswers}>
{/*                <FormControl component="fieldset">
                    <FormLabel component="legend">Gender</FormLabel>
                    <RadioGroup aria-label="gender" name="gender1" onChange={handleChange}>
                        <FormControlLabel value="female" control={<Radio />} label="Female" />
                        <FormControlLabel value="male" control={<Radio />} label="Male" />
                        <FormControlLabel value="other" control={<Radio />} label="Other" />
                        <FormControlLabel value="disabled" disabled control={<Radio />} label="(Disabled option)" />
                    </RadioGroup>
    </FormControl>*/}
                {
                    !question.is_open ? (
                        <FormControl component="fieldset" style={{ width: "100%" }}>
                            <RadioGroup style={{ flexDirection: "row", justifyContent: "space-around" }} aria-label={question.id} onChange={handleChange}>
                                {question.reponses.map((r, index) => {
                                    return (
                                        <FormControlLabel key={index} value={''+r.id+''} control={<Radio />} label={r.body} />
                                    )
                                })}
                            </RadioGroup>
                        </FormControl>
                    )
                        : (<TextField style={{ margin: 8 }}
                            inputRef={openQst}
                            fullWidth id="standard-basic"  placeholder="votre suggestion" />)
                }
            </div>
            <Divider className={classes.divider} />
        </div>
    );
}
const useStyles = makeStyles({
    divAnswers: {
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
        justifyContent: "flex-end"
    },
    pQst: {
        color: "#4829B2",
        fontWeight: "bold"
    },
    divider: {
        backgroundColor: "#b3294e",
        height: "0.2rem"
    }

});
export default Qst;