import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

export default function ModalQuiz({module,open,goToQuiz,handleClose}) {
  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{module !== null && (module.slug+" : Quiz")}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
          Veuillez passez le quiz de vos pré-requis avant de commencer la formation
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Annuler
          </Button>
          <Button onClick={()=>{goToQuiz()}} color="primary" autoFocus>
            Passer Quiz
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
