import React, { memo } from "react";
import PropTypes from "prop-types";
import { Switch } from "react-router-dom";
import { withStyles } from "@material-ui/core";
import ParticipantSession from "./Interactive/ParticipantSession";
import ProtectedRoute from "../../shared/components/ProtectedRoute";
import Cours from "./Formations/Cours"; 
import Sheraps from './Sheraps/Home';
import Quiz from './Quiz/Quiz';
import Evaluations from './Evaluations/Evaluations';
import ModuleToLean from "./Formations/ModuleToLearn";
import Questionnaire from './Evaluations/Questionnaire';
import { AuthContext } from '../Providers/AuthProvider'
import FormateurSession from './Interactive/FormateursSession';
import CoursFormateurs from './Formations/CoursFormateurs';
import ModulesStat from "./Statistique/ModulesStat";
import ModuleStat from './Statistique/ModuleStat'

const styles = (theme) => ({
  wrapper: {
    margin: theme.spacing(1),
    width: "auto",
    [theme.breakpoints.up("xs")]: {
      width: "95%",
      marginLeft: "auto",
      marginRight: "auto",
      marginTop: theme.spacing(4),
      marginBottom: theme.spacing(4),
    },
    [theme.breakpoints.up("sm")]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      width: "90%",
      marginLeft: "auto",
      marginRight: "auto",
    },
    [theme.breakpoints.up("md")]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      width: "82.5%",
      marginLeft: "auto",
      marginRight: "auto",
    },
    [theme.breakpoints.up("lg")]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      width: "84%",
      marginLeft: "auto",
      marginRight: "8vh",
    },
  },
});

function Routing(props) {
  const {
    classes,
    pushMessageToSnackbar,
    transactions,
    toggleAccountActivation,
    CardChart,
    statistics,
    targets,
    setTargets,
    isAccountActivated,
    selectDashboard,
    selectPosts,
    selectFormateurs,
    selectSubscription,
    openAddBalanceDialog,
  } = props;
  const { user } = React.useContext(AuthContext);

  return (
    <div className={classes.wrapper}>
      <Switch>
        <ProtectedRoute
          path="/c/Module/:id"
          component={ModuleToLean}
          selectPosts={selectPosts}
        />
        <ProtectedRoute
          path="/c/Formations"
          component={user.role_id === 3  || user.role_id == 4 ? Cours : CoursFormateurs}
          selectPosts={selectPosts}
        />
        <ProtectedRoute
          path="/c/Formateurs"
          component={Sheraps}
          transactions={transactions}
          pushMessageToSnackbar={pushMessageToSnackbar}
          selectFormateurs={selectFormateurs}
          openAddBalanceDialog={openAddBalanceDialog}
        />
        <ProtectedRoute
          path="/c/Evaluations"
          component={Evaluations}
          transactions={transactions}
          pushMessageToSnackbar={pushMessageToSnackbar}
          selectSubscription={selectSubscription}
          openAddBalanceDialog={openAddBalanceDialog}
        />
        <ProtectedRoute
          path="/c/Statistiques"
          component={ModulesStat} 
          transactions={transactions}
          pushMessageToSnackbar={pushMessageToSnackbar}
          selectSubscription={selectSubscription}
          openAddBalanceDialog={openAddBalanceDialog}
        />
        <ProtectedRoute
          path="/c/reporting/:id"
          component={ModuleStat}
          transactions={transactions}
          pushMessageToSnackbar={pushMessageToSnackbar}
          selectSubscription={selectSubscription}
          openAddBalanceDialog={openAddBalanceDialog}
        />
        <ProtectedRoute
          path="/c/Evaluate/:id"
          component={Questionnaire}
          selectSubscription={selectSubscription}
        />
        <ProtectedRoute
          path="/c/Quiz/:id"
          component={Quiz}
        />
        <ProtectedRoute
          path="/c/Interactive"
          component={user.role_id === 3 || user.role_id == 4 ? ParticipantSession : FormateurSession}
          toggleAccountActivation={toggleAccountActivation}
          pushMessageToSnackbar={pushMessageToSnackbar}
          CardChart={CardChart}
          statistics={statistics}
          targets={targets}
          setTargets={setTargets}
          isAccountActivated={isAccountActivated}
          selectDashboard={selectDashboard}
        />
      </Switch>
    </div>
  );
}

Routing.propTypes = {
  classes: PropTypes.object.isRequired,
  EmojiTextArea: PropTypes.elementType,
  ImageCropper: PropTypes.elementType,
  Dropzone: PropTypes.elementType,
  DateTimePicker: PropTypes.elementType,
  pushMessageToSnackbar: PropTypes.func,
  setTargets: PropTypes.func.isRequired,
  setPosts: PropTypes.func.isRequired,
  posts: PropTypes.arrayOf(PropTypes.object).isRequired,
  transactions: PropTypes.arrayOf(PropTypes.object).isRequired,
  toggleAccountActivation: PropTypes.func,
  CardChart: PropTypes.elementType,
  statistics: PropTypes.object.isRequired,
  targets: PropTypes.arrayOf(PropTypes.object).isRequired,
  isAccountActivated: PropTypes.bool.isRequired,
  selectDashboard: PropTypes.func.isRequired,
  selectPosts: PropTypes.func.isRequired,
  selectSubscription: PropTypes.func.isRequired,
  openAddBalanceDialog: PropTypes.func.isRequired,
};

export default withStyles(styles, { withTheme: true })(memo(Routing));
