import React, { Fragment, useState, useEffect } from "react";
import PropTypes from "prop-types";
import { Grid, Box } from "@material-ui/core";
import QuizItem from '../Quiz/QuizItem';
import Loading from '../Loading/Loading';
import ModalQuiz from '../Quiz/ModalQuiz';
import { getFormateurModules, makeModuleLive } from '../../../AxiosServices/CoursService';
import { useHistory } from "react-router-dom";
import { AuthContext } from '../../Providers/AuthProvider'




function Dashboard(props) {
    const {
        selectDashboard,
    } = props;
    const history = useHistory();
    const { user } = React.useContext(AuthContext);
    const [loading, setLoading] = React.useState(true);
    const [Modules, setModules] = React.useState([]);
    useEffect(() => {
        selectDashboard()
        getFormateurModules().then((r) => {
            setModules(r.data.data);
            setLoading(false);
        }).catch((e) => console.log(e))

    }, [])

    const refresh = () => {
        getFormateurModules().then((r) => {
            setModules(r.data.data);
            setLoading(false);
        }).catch((e) => {
            setLoading(false);
            alert('verifier votre connexion');
        })
    }
    const go = (e) => {
        makeModuleLive(e.id).then((e) => {
            {   /*     let helpModules=[...Modules];
                var foundIndex = helpModules.findIndex(x => x.id == e.id);
                helpModules[foundIndex] = e.data.data;
                console.log(helpModules[foundIndex])
            setModules(helpModules); */}
            refresh()
        }).catch((e) => {
            console.log('error');
        })
        if(!e.is_live)
        window.open(e.live_url + '&username=' + user.nom_complet + "&r=" + user.role_id, '_blank')
    }
    if (loading)
        return (<Loading></Loading>)

    return (
        <Fragment>
            {

                <Grid style={{ marginLeft: '2%' }} container spacing={3}>
                    {
                        Modules.map((e, index) => {
                            return (
                                <Grid
                                    key={index}
                                    item
                                    xs={12}
                                    sm={6}
                                    lg={4}
                                    data-aos="zoom-in-up"
                                >
                                    <QuizItem
                                        module={e}
                                        role={user.role_id}
                                        go={() => { go(e) }}
                                    />
                                </Grid>

                            )

                        })

                    }
                </Grid>

            }
        </Fragment>
    );
}

Dashboard.propTypes = {
    CardChart: PropTypes.elementType,
    statistics: PropTypes.object.isRequired,
    toggleAccountActivation: PropTypes.func,
    pushMessageToSnackbar: PropTypes.func,
    targets: PropTypes.arrayOf(PropTypes.object).isRequired,
    setTargets: PropTypes.func.isRequired,
    isAccountActivated: PropTypes.bool.isRequired,
    selectDashboard: PropTypes.func.isRequired,
};

export default Dashboard;
