import CanvasJSReact from '../../../assets/canvasjs.react';
import Loading from '../Loading/Loading';
//var CanvasJSReact = require('./canvasjs.react');


import React from 'react';
import {getStatQuiz} from '../../../AxiosServices'
export default function PreQuiz({id}) {
    const [loading,setLoading]=React.useState(true);
    var CanvasJS = CanvasJSReact.CanvasJS;
    var CanvasJSChart = CanvasJSReact.CanvasJSChart;
    let [dataPointsL,setDataPoint]=React.useState([]);
    React.useEffect(()=>{
        getStatQuiz(id).then((r)=> {
            setDataPoint(r.data.data.stats_score);
            setLoading(false);
        }).catch((e)=>{
            console.log(e)
        })
    },[])

    const options = {
        title: {
            text: "le pourcentage des  pré-requis pour les participants "
        },
        data: [{
            type: "column",
            dataPoints: dataPointsL
        }]
    } 
    if(loading)
    return(<Loading></Loading>)

    return (
        <div style={{marginLeft:'2%'}}>
            <CanvasJSChart options={options}
            /* onRef = {ref => this.chart = ref} */
            />
        </div>
    );
}
