import React, { Fragment, useRef, useCallback, useState } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import classNames from "classnames";
import { AuthContext } from '../../Providers/AuthProvider';
import {
  AppBar,
  Toolbar,
  Typography,
  Avatar,
  Drawer,
  List,
  IconButton,
  ListItem,
  ListItemIcon,
  ListItemText,
  Hidden,
  Tooltip,
  Box,
  withStyles,
  isWidthUp,
  withWidth,
} from "@material-ui/core";
import DashboardIcon from "@material-ui/icons/Dashboard";
import ImageIcon from "@material-ui/icons/Image";
import SearchIcon from '@material-ui/icons/Search';
import AccountBalanceIcon from "@material-ui/icons/AccountBalance";
import MenuBookIcon from '@material-ui/icons/MenuBook';
import PowerSettingsNewIcon from "@material-ui/icons/PowerSettingsNew";
import MenuIcon from "@material-ui/icons/Menu";
import SupervisorAccountIcon from "@material-ui/icons/SupervisorAccount";
import MessagePopperButton from "./MessagePopperButton";
import SideDrawer from "./SideDrawer";
import Balance from "./Balance";
import NavigationDrawer from "../../../shared/components/NavigationDrawer";
import { logout } from '../../../AxiosServices/AuthService';

const styles = (theme) => ({
  appBar: {
    boxShadow: theme.shadows[6],
    backgroundColor: theme.palette.common.white,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    [theme.breakpoints.down("xs")]: {
      width: "100%",
      marginLeft: 0,
    },
  },
  appBarToolbar: {
    display: "flex",
    justifyContent: "space-between",
    paddingLeft: theme.spacing(1),
    paddingRight: theme.spacing(1),
    [theme.breakpoints.up("sm")]: {
      paddingLeft: theme.spacing(2),
      paddingRight: theme.spacing(2),
    },
    [theme.breakpoints.up("md")]: {
      paddingLeft: theme.spacing(3),
      paddingRight: theme.spacing(3),
    },
    [theme.breakpoints.up("lg")]: {
      paddingLeft: theme.spacing(4),
      paddingRight: theme.spacing(4),
    },
  },
  accountAvatar: {
    backgroundColor: theme.palette.secondary.main,
    height: 24,
    width: 24,
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
    [theme.breakpoints.down("xs")]: {
      marginLeft: theme.spacing(1.5),
      marginRight: theme.spacing(1.5),
    },
  },
  drawerPaper: {
    height: "100%vh",
    whiteSpace: "nowrap",
    border: 0,
    width: theme.spacing(18),
    overflowX: "hidden",
    marginTop: theme.spacing(8),
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(30),
    },
    backgroundColor: theme.palette.common.black,
  },
  smBordered: {
    [theme.breakpoints.down("xs")]: {
      borderRadius: "50% !important",
    },
  },
  menuLink: {
    textDecoration: "none",
    color: theme.palette.text.primary,
  },
  iconListItem: {
    width: "auto",
    borderRadius: theme.shape.borderRadius,
    paddingTop: 11,
    paddingBottom: 11,
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  textPrimary: {
    color: theme.palette.primary.main,
  },
  mobileItemSelected: {
    backgroundColor: `${theme.palette.primary.main} !important`,
  },
  brandText: {
    fontFamily: "'Baloo Bhaijaan', cursive",
    fontWeight: 400,
  },
  username: {
    paddingLeft: 0,
    paddingRight: theme.spacing(2),
  },
  justifyCenter: {
    justifyContent: "center",
  },
  permanentDrawerListItem: {
    justifyContent: "center",
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
});

function NavBar(props) {
  const { selectedTab, messages, classes, width, openAddBalanceDialog } = props;
  // Will be use to make website more accessible by screen readers
  const links = useRef([]);
  const auth = React.useContext(AuthContext);

  const [isMobileOpen, setIsMobileOpen] = useState(false);
  const [isSideDrawerOpen, setIsSideDrawerOpen] = useState(false);
  let deconnecte = () => {
    logout().then((r) => {
      localStorage.removeItem("access_token");
      localStorage.removeItem("user");
      auth.setToken('');
      auth.setUser('');
    window.location.reload()
    }).catch((e) => {
      console.log(e);
    })
  }

  const openMobileDrawer = useCallback(() => {
    setIsMobileOpen(true);
  }, [setIsMobileOpen]);

  const closeMobileDrawer = useCallback(() => {
    setIsMobileOpen(false);
  }, [setIsMobileOpen]);

  const openDrawer = useCallback(() => {
    setIsSideDrawerOpen(true);
  }, [setIsSideDrawerOpen]);

  const closeDrawer = useCallback(() => {
    setIsSideDrawerOpen(false);
  }, [setIsSideDrawerOpen]);
  const user = JSON.parse(localStorage.getItem('user'));
  const username = user ? user.nom_complet : '';

  const menuItems =
  user.role_id === 2 ?
  
   [{
     link: "/c/Formations",
     name: "Formations",
     onClick: closeMobileDrawer,
     icon: {
       desktop: (
         <MenuBookIcon
           className={
             selectedTab === "Formations" ? classes.textPrimary : "text-white"
           }
           fontSize="small"
         />
       ),
       mobile: <MenuBookIcon className="text-white" />,
     },
   },
   {
     link: "/c/Interactive",
     name: "Formation distancielle ",
     subname: "distancielle ",
     onClick: closeMobileDrawer,
     icon: {
       desktop: (
         <DashboardIcon
           className={
             selectedTab === "Quiz" ? classes.textPrimary : "text-white"
           }
           fontSize="small"
         />
       ),
       mobile: <DashboardIcon className="text-white" />,
     },
   },
   {
     link: "/c/Statistiques",
     name: "Tableau de bord",
     onClick: closeMobileDrawer,
     icon: {
       desktop: (
         <AccountBalanceIcon
           className={
             selectedTab === "Evaluations"
               ? classes.textPrimary
               : "text-white"
           }
           fontSize="small"
         />
       ),
       mobile: <AccountBalanceIcon className="text-white" />,
     },
   },
   {
     link: "/c/Formateurs",
     name: "Formateurs",
     onClick: closeMobileDrawer,
     icon: {
       desktop: (
         <SearchIcon
           className={
             selectedTab === "Formateurs"
               ? classes.textPrimary
               : "text-white"
           }
           fontSize="small"
         />
       ),
       mobile: <SearchIcon className="text-white" />,
     },
   },
   {
     link: "/",
     name: "Se déconnecter",
     icon: {
       desktop: (
         <PowerSettingsNewIcon className="text-white" fontSize="small" />
       ),
       mobile: <PowerSettingsNewIcon className="text-white" />,
     },
   },
   ]
:

(user.role_id === 3) ?
  [
   {
     link: "/c/Formations",
     name: "Formations",
     onClick: closeMobileDrawer,
     icon: {
       desktop: (
         <MenuBookIcon
           className={
             selectedTab === "Formations" ? classes.textPrimary : "text-white"
           }
           fontSize="small"
         />
       ),
       mobile: <MenuBookIcon className="text-white" />,
     },
   },
   {
     link: "/c/Interactive",
     name: "Formation distancielle ",
     subname: "distancielle ",
     onClick: closeMobileDrawer,
     icon: {
       desktop: (
         <DashboardIcon
           className={
             selectedTab === "Quiz" ? classes.textPrimary : "text-white"
           }
           fontSize="small"
         />
       ),
       mobile: <DashboardIcon className="text-white" />,
     },
   },
   {
     link: "/c/Evaluations",
     name: "Evaluations",
     
     onClick: closeMobileDrawer,
     icon: {
       desktop: (
         <AccountBalanceIcon
           className={
             selectedTab === "Evaluations"
               ? classes.textPrimary
               : "text-white"
           }
           fontSize="small"
         />
       ),
       mobile: <AccountBalanceIcon className="text-white" />,
     },
   },
 
   {
     link: "/c/Formateurs",
     name: "Formateurs",
     onClick: closeMobileDrawer,
     icon: {
       desktop: (
         <SearchIcon
           className={
             selectedTab === "Formateurs"
               ? classes.textPrimary
               : "text-white"
           }
           fontSize="small"
         />
       ),
       mobile: <SearchIcon className="text-white" />,
     },
   },
   {
     link: "/",
     name: "Se déconnecter",
     icon: {
       desktop: (
         <PowerSettingsNewIcon className="text-white" fontSize="small" />
       ),
       mobile: <PowerSettingsNewIcon className="text-white" />,
     },
   },
 ]  :

 [
   {
     link: "/c/Formations",
     name: "Formations",
     onClick: closeMobileDrawer,
     icon: {
       desktop: (
         <MenuBookIcon
           className={
             selectedTab === "Formations" ? classes.textPrimary : "text-white"
           }
           fontSize="small"
         />
       ),
       mobile: <MenuBookIcon className="text-white" />,
     },
   },
   {
     link: "/c/Interactive",
     name: "Formation distancielle ",
     subname: "distancielle ",
     onClick: closeMobileDrawer,
     icon: {
       desktop: (
         <DashboardIcon
           className={
             selectedTab === "Quiz" ? classes.textPrimary : "text-white"
           }
           fontSize="small"
         />
       ),
       mobile: <DashboardIcon className="text-white" />,
     },
   },
   // {
   //   link: "/c/Evaluations",
   //   name: "Evaluations",
     
   //   onClick: closeMobileDrawer,
   //   icon: {
   //     desktop: (
   //       <AccountBalanceIcon
   //         className={
   //           selectedTab === "Evaluations"
   //             ? classes.textPrimary
   //             : "text-white"
   //         }
   //         fontSize="small"
   //       />
   //     ),
   //     mobile: <AccountBalanceIcon className="text-white" />,
   //   },
   // },
 
   {
     link: "/c/Formateurs",
     name: "Formateurs",
     onClick: closeMobileDrawer,
     icon: {
       desktop: (
         <SearchIcon
           className={
             selectedTab === "Formateurs"
               ? classes.textPrimary
               : "text-white"
           }
           fontSize="small"
         />
       ),
       mobile: <SearchIcon className="text-white" />,
     },
   },
   {
     link: "/",
     name: "Se déconnecter",
     icon: {
       desktop: (
         <PowerSettingsNewIcon className="text-white" fontSize="small" />
       ),
       mobile: <PowerSettingsNewIcon className="text-white" />,
     },
   },
 ]
   ;
  return (
    <Fragment>
      <AppBar position="sticky" className={classes.appBar}>
        <Toolbar className={classes.appBarToolbar}>
          <Box display="flex" alignItems="center" style={{ width: '32%' }} >
            <Hidden smUp>
              <Box mr={1}>
                <IconButton
                  aria-label="Open Navigation"
                  onClick={openMobileDrawer}
                  color="primary"
                >
                  <MenuIcon />
                </IconButton>
              </Box>
            </Hidden>
            <Hidden xsDown>
              <Typography
                variant="h4"
                className={classes.brandText}
                display="inline"
                color="primary"
              >
                BEREXIA {' '}
              </Typography>
              <Typography
                variant="h4"
                className={classes.brandText}
                display="inline"
                color="secondary"
              >
                E-Learning
              </Typography>
            </Hidden>
          </Box>

          <Box
            display="flex"
            justifyContent="flex-end"
            alignItems="center"
            width="100%"
          >
            { /*isWidthUp("sm", width) && (
              <Box mr={3}>
                <Balance
                  balance={2573}
                  openAddBalanceDialog={openAddBalanceDialog}
                />
              </Box>
            )*/}
            {//<MessagePopperButton messages={messages} />
            }
            <ListItem
              disableGutters
              className={classNames(classes.iconListItem, classes.smBordered)}
            >

              {isWidthUp("sm", width) && (
                <ListItemText
                  className={classes.username}
                  primary={
                    <Typography color="textPrimary">{username}</Typography>
                  }
                />
              )}
            </ListItem>
          </Box>
          { /*         <IconButton
            onClick={openDrawer}
            color="primary"
            aria-label="Open Sidedrawer"
          >
            <SupervisorAccountIcon />
          </IconButton>
<SideDrawer open={isSideDrawerOpen} onClose={closeDrawer} />*/}
        </Toolbar>
      </AppBar>
      <Hidden xsDown>
        <Drawer //  both drawers can be combined into one for performance
          variant="permanent"
          classes={{
            paper: classes.drawerPaper,
          }}
          open={false}
        >
          <List>
            {menuItems.map((element, index) => (
              <Link
                to={element.link}
                className={classes.menuLink}
                onClick={element.onClick}
                key={index}
                ref={(node) => {
                  links.current[index] = node;
                }}
              >
                <Tooltip
                  title={element.name}
                  placement="right"
                  key={element.name}
                >
                  <ListItem
                    selected={selectedTab === element.name}
                    button
                    divider={index !== menuItems.length - 1}
                    className={classes.permanentDrawerListItem}
                    onClick={() => {
                      element.name === "Se déconnecter" ? deconnecte() :
                        links.current[index].click();
                    }}
                    aria-label={
                      element.name === "Se déconnecter"
                        ? "Logout"
                        : `Go to ${element.name}`
                    }
                  >
                    <ListItemIcon className={classes.justifyCenter}>
                      {element.icon.desktop}
                    </ListItemIcon>
                    <p style={{
                      flex: '1 1 auto', minWidth: 0, marginTop: '4px', marginBottom: '4px',
                      color: 'white'
                    }}>{element.name} </p>
                  </ListItem>
                </Tooltip>
              </Link>
            ))}
          </List>
        </Drawer>
      </Hidden>
      <NavigationDrawer
        menuItems={menuItems.map((element) => ({
          link: element.link,
          name: element.name,
          icon: element.icon.mobile,
          onClick: element.onClick,
        }))}
        anchor="left"
        open={isMobileOpen}
        selectedItem={selectedTab}
        onClose={closeMobileDrawer}
      />
    </Fragment >
  );
}

NavBar.propTypes = {
  messages: PropTypes.arrayOf(PropTypes.object).isRequired,
 // selectedTab: PropTypes.string.isRequired,
  width: PropTypes.string.isRequired,
  classes: PropTypes.object.isRequired,
  openAddBalanceDialog: PropTypes.func.isRequired,
};

export default withWidth()(withStyles(styles, { withTheme: true })(NavBar));
