import React, { useEffect, useState } from 'react';
import PropTypes from "prop-types";
import classNames from "classnames";
import { Grid, Box, makeStyles } from "@material-ui/core";
import ModalQuiz from '../Quiz/ModalQuiz';
//import { courses } from "../../dummy_data/persons";
import CardItem from '../Formations/CardItem';
import { getModules, apiUrl } from '../../../AxiosServices';
//import { Modules } from '../../dummy_data/persons';
import { useHistory } from "react-router-dom";
import Loading from '../Loading/Loading';

function Evaluations(props) {
    const [modules, setModules] = useState([]);
    const [loading, setLoading] = useState(true);
    const history=useHistory();
    const [selectedModule,setSelectedModule]=useState(null);
    const image=`${process.env.PUBLIC_URL}/images/logged_out/evaluation.jpg`;
    const go = (e)=>{
            setSelectedModule(e)
            history.push('/c/Evaluate/'+e.id);
    }
    const {
        selectSubscription,
    } = props;

    {
        React.useEffect(() => {
            getModules().then((r)=> {
                setModules(r.data.data);
                setLoading(false);
            }).catch((e)=>{
                setLoading(false);
                alert('verifier votre connexion');
            })
            selectSubscription();

        }, []);
    }
    const classes = styles();
    if(loading)
    {
        return (<Loading></Loading>)
    }


    return (
        <div>
            <div
                style={{ flexDirection: "column" }}
                className={classNames(classes.wrapper)}
            >
                <div className={classes.blogContentWrapper}>
                    <Grid container spacing={1}>
                        {
                            modules.map((e, index) => {
                                return (
                                    <Grid
                                        key={index}
                                        item
                                        xs={12}
                                        sm={6}
                                        lg={4}
                                        className={classes.cardWrapper}
                                        data-aos="zoom-in-up"
                                    >
                                        <CardItem
                                            text={'Evaluer le '+e.slug}
                                            image={image}
                                            open={()=>{go(e)}}
                                            isOwner={e.is_owner}
                                        />
                                    </Grid> 

                                )

                            })

                        }
                    </Grid>
                </div>
            </div>

        </div>
    );
}
const styles = makeStyles((theme) => ({
    blogContentWrapper: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
        [theme.breakpoints.up("sm")]: {
            marginLeft: theme.spacing(6),
            marginRight: theme.spacing(4),
        },
        maxWidth: 1280,
        width: "100%",
    },
    wrapper: {
        minHeight: "60vh",
    },
    noDecoration: {
        textDecoration: "none !important",
    },
    image: {
        backgroundImage: `url(${process.env.PUBLIC_URL}/images/logged_out/courses.jpg)`,
        backgroundRepeat: 'no-repeat',
        backgroundColor:
            theme.palette.type === 'light' ? theme.palette.grey[50] : theme.palette.grey[900],
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        height: '45vh',
        "&::before": {
            content: "''",
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: ' rgba(0,0,0,.75)',
            height: '45%',
            width: '100%',
        }
    },
    divTitle: {
        position: 'relative',
    },
    title: {
        color: 'white',
        marginLeft: "10%", 
        fontSize: 60,
        marginBottom: 0
    },
    subtitle: {
        color: '#b3294e',
        marginLeft: "10%",
        fontSize: 30,
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    filtres: {
        margin: "2%",
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between"
    },
    search: {
        width: "30%",
        border: 'solid 2px #b3294e',
        backgroundColor: "white",
        borderRadius: 20,
        '&:hover': {
            backgroundColor: "white",
        },
        // position:"absolute",
        //  top:"-8%",
        // left:"1%",
    },
    searchIcon: {
        padding: theme.spacing(0, 2),
        height: '6%',
        position: 'absolute',
        pointerEvents: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        color: "#b3294e"
    },
    inputInput: {
        padding: theme.spacing(1, 1, 1, 0),
        // fontSize:"3rem",
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('sm')]: {
            width: '12ch',
            '&:focus': {
                width: '20ch', 
            },
        },
    },
}));

Evaluations.propTypes = {
    selectSubscription: PropTypes.func.isRequired,
};
export default Evaluations;