import React from 'react';
import { makeStyles, useTheme, Grid, MobileStepper, Button } from '@material-ui/core';
import { KeyboardArrowLeft, KeyboardArrowRight, NotInterested } from '@material-ui/icons';
import Answer from './Answer';
import { getQuizByModule,sendScore, apiUrl } from '../../../AxiosServices';
import { useParams, useHistory } from 'react-router-dom';
import Loading from '../Loading/Loading';
import ModalResultat from './ModalResultat';
import './Quiz.css';
const useStyles = makeStyles({
    root: {
        flexGrow: 1,
    },
});

export default function Exercice() {
    let { id } = useParams();
    let history = useHistory();
    let [quiz,setQuiz] = React.useState(null);
    let [open,setOpen]=React.useState(false);
    let [loading, setLoading] = React.useState(true);
    let [selectedanswer, Setsa] = React.useState(-1);
    let [reponses, setReponses] = React.useState(0);
    let [answers, Setanswers] = React.useState(0);
    let [Questions, setQuestions] = React.useState([]);
    let [isEnded, setEnded] = React.useState(false);

    React.useEffect(() => {
        getQuizByModule(id).then((r)=>{
            setQuestions(r.data.data.quiz.questions);
            setLoading(false);
            setQuiz(r.data.data);
        }).catch((e)=>{
            console.log(e);
        })
    }, [])
    const handleClickOpen = () => {
        setOpen(true);     
    };
    
    const handleClose = () => {
        setOpen(false);
    };
    const next = () => {
        if(!isEnded){
            let value = reponses + answers;
            Setanswers(value);
            Setsa(-1);
            if (activeStep === Questions.length - 1)
            {
                sendScore({
                    quiz_id:quiz.quiz.id,
                    score: Math.floor( (value / Questions.length) * 100 ) 
                })
                setEnded(true)
                handleClickOpen();
        }
        }

    }

    const classes = useStyles();
    const theme = useTheme();
    const [activeStep, setActiveStep] = React.useState(0);

    const handleNext = () => {
        next();
        if (activeStep === Questions.length - 1 )
        return 0;
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };


    if(loading)
        {return <Loading></Loading>}
    return (
        <div className="list" style={{ marginLeft: '4%' }} >
            <ModalResultat open={open} handleClose={handleClose} 
            reponses={answers}
            is_live={quiz ? quiz.is_live: null} lien={quiz ? quiz.live_url : null} totalQst={Questions.length} module_id={id} history={history} ></ModalResultat>
            <h1>
                Quiz du module {id}: 
            </h1>
            <div  style={{ textAlign: "center",marginTop: '2%' }}>

                <span style={{color:'#4829B2',fontWeight:'600'}} > Question {activeStep + 1} / {Questions.length}</span>
                <MobileStepper
                    variant="progress"
                    steps={Questions.length}
                    position="static"
                    activeStep={activeStep}
                    className={classes.root}
                />
                <div> <h2>{Questions[activeStep].body}</h2></div>
                <Grid container spacing={3} style={{ marginTop: "5%" }} >
                    {

                        Questions[activeStep].reponses.map((el, index) => (
                            <Answer
                                selected={index === selectedanswer}
                                select={() => { Setsa(index); setReponses(el.iscorrect) }}
                                key={index}
                                text={el.body}
                            ></Answer>
                        ))
                    }

                </Grid>
              <div className="Buttons">
         {/*             <Button className="medSize" onClick={handleBack} disabled={activeStep === 0}>
                        {theme.direction === 'rtl' ? <KeyboardArrowRight className="medSize" /> : <KeyboardArrowLeft className="medSize" />}
                    Revenir
                </Button>*/}
                    <Button variant="contained" color="primary" className="medSize"  disabled={activeStep === Questions.length  } onClick={ isEnded ? handleClickOpen : handleNext} >
                       {activeStep === Questions.length - 1 ? 'Terminer' : 'Suivant'}
                {theme.direction === 'rtl' ? <KeyboardArrowLeft className="medSize" /> : <KeyboardArrowRight className="medSize" />}
                    </Button>
                </div>

            </div>

        </div>
    );
}