import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Grid, Box } from '@material-ui/core';
import Qst from './Qst';
import { useParams } from 'react-router-dom'
import { getQuestionnaire, sendEvaluation } from '../../../AxiosServices/CoursService'
import Loading from '../Loading/Loading';
import Thanks from './Thanks';


function Questionnaire({ event }) {
    const classes = useStyles();
    const { id } = useParams();
    const [Questions, setQuestions] = React.useState([]);
    const [loading, setLoading] = React.useState(true);
    const [showTx, setShowTx] = React.useState(false);
    const [answers, setAnswers] = React.useState([]);
    const openQst = React.useRef()
    React.useEffect(() => {
        getQuestionnaire(id).then((r) => {
            setQuestions(r.data.data)
            setLoading(false);
        }).catch((e) => {
            setLoading(false);
            alert('verifier votre connexion');
        })
    }, [])
    const getQsts = () => {
        if (Questions)
            return Questions.evaluationquestions.map((e, index) => {
                return <Qst openQst={openQst} setAnswer={setAnswer} key={index} number={index} question={e} />
            }
            )

    }
    const setAnswer = (r) => {
        let arr = [...answers]
        const found = arr.some(el => el.question === r.question);
        if (!found) {
            arr.push(r);
        }
        else {
            let index = arr.findIndex(x => x.question === r.question);
            arr[index] = r
        }
        setAnswers(arr);
        return arr;
    }
    const send = () => {
        let n = Questions.evaluationquestions.length;
        let rep = setAnswer({
            question: Questions.evaluationquestions[n - 1].id,
            reponse: openQst.current.value
        })
        let data = {
            module_id: id,
            evaluations: rep
        }
        {
            sendEvaluation(data).then((r) => {
                setShowTx(true);
            }).catch((e) => {
                console.log(e);
            });
        }
    }
    if (loading)
        return (<Loading></Loading>)
    if (Questions.evaluation_passed) {
        return (<Thanks text={"Vous avez déja répondu à cet questionnaire"} ></Thanks>)

    }
    if (showTx) {
        return (<Thanks text={"Merci d'avoir répondu au questionnaire"} ></Thanks>)

    }
    return (
        <div style={{ marginLeft: '4%' }}>

            <Grid container spacing={1}>
                <Grid
                    item
                    xs={12}
                    sm={6}
                    md={12}>
                    <Box style={{ backgroundColor: 'white' }}>


                        <h2 style={{ paddingTop: '2%', textAlign: 'center', color: "#b3294e" }}>Evaluation du Module {id} </h2>

                        <div >
                            {
                                getQsts()
                            }
                            <div style={{ display: "flex", justifyContent: 'flex-end' }}>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    className={classes.button}
                                    style={{ marginBottom: '2%', marginRight: "2%" }}
                                    onClick={() => { send() }}
                                >
                                    Envoyer
                            </Button>

                            </div>



                        </div>

                    </Box>
                </Grid>

            </Grid>

        </div>
    );
}


const useStyles = makeStyles({
    table: {
        minWidth: 650,
    },
    add: {
        color: "white",
        position: "absolute",
        top: "1rem",
        right: "1rem"
    },
    cardCategoryWhite: {
        color: "rgba(255,255,255,.62)",
        margin: "0",
        fontSize: "14px",
        marginTop: "0",
        marginBottom: "0"
    },
    cardTitleWhite: {
        color: "#FFFFFF",
        marginTop: "0px",
        minHeight: "auto",
        fontWeight: "300",
        fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
        marginBottom: "3px",
        textDecoration: "none",
        textAlign: "center"
    },
    answer: {
        border: "solid 2px teal",
        borderRadius: 10,
        padding: 5,
        display: "flex",
        justifyContent: "center",
        boxShadow: "2px 3px 5px -2px #000000",
    },
    IconRightAnswer: {
        color: "green",
        marginLeft: 10
    },
    IconBadAnswer: {
        color: "red",
        marginLeft: 10
    },
    button: {

    },
    Qst: {
        fontSize: "1.3rem",
        textAlign: "center",
        fontWeight: 500
    },
    divAnswer: {
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
    },
    spanAnswer: {
        textAlign: "center",
        fontSize: "1.5rem",
        margin: "0.5rem",
        fontWeight: "bold"
    },
    divAnswers: {
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap"
    }
});

export default Questionnaire;